import React, { Component } from "react";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import ToolkitProvider, { Search } from "react-bootstrap-table2-toolkit";
import Skeleton from "react-loading-skeleton";
import SelectSearch from "react-select-search";

const { SearchBar } = Search;
class RekapBonCustomer extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  onChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    return (
      <div className="container-fluid">
        <div className="col-lg-12 col-md-12">
          <div className="card">
            <div className="card-body">
              <div className="row">
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Tanggal</label>
                  <input
                    type="date"
                    name="tanggal"
                    className="form-control"
                    onChange={this.onChange("tanggal")}
                  />
                </div>
                <div className="col-lg-4 col-md-4">
                  <label htmlFor="">Nama Toko</label>
                  <SelectSearch
                    options={this.state.items}
                    search
                    placeholder="Silahkan Pilih"
                  />
                </div>
                <div className="col-md-4 col-lg-4">
                  <label htmlFor="">Customer</label>
                  <input
                    type="text"
                    name="customer"
                    className="form-control"
                    onChange={this.onChange("customer")}
                  />
                </div>
                <div className="col-lg-12 mt-5">
                  {this.props.returnPenjualan ? (
                    <ToolkitProvider
                      keyField="id"
                      data={this.props.returnPenjualan}
                      columns={this.state.columns}
                      search
                    >
                      {(props) => (
                        <div className="form-group">
                          <div className="text-right">
                            <SearchBar
                              {...props.searchProps}
                              className="form-control"
                              placeholder="Pencarian"
                              style={{ margin: 0, top: "50%" }}
                            />
                          </div>
                          <BootstrapTable
                            {...props.baseProps}
                            striped
                            hover
                            condensed
                            bordered={true}
                            headerClasses="header-table"
                            pagination={paginationFactory()}
                          />
                        </div>
                      )}
                    </ToolkitProvider>
                  ) : (
                    <Skeleton width={"100%"} height={100} />
                  )}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default RekapBonCustomer;
